import React from 'react';
import {    
    Container, 
    Col,
    Row
    } from 'reactstrap';
import policyStyles from '../style/Policy.css';


const Policy = () => {   
    return (
        <Container>
            <Row className="content-policy">
                <Col>
                    <h3>Polityka prywatności</h3>
                    <p>                
                    1. Z jakim dokumentem masz do czynienia?
                    Informacje zawarte w Polityce mają charakter ogólny. Szczegółowa informacja dotycząca przetwarzania konkretnych danych osobowych jest udostępniana każdorazowo podczas ich pozyskiwania w treści klauzuli informacyjnej zamieszczonej w widocznym i łatwo dostępnym miejscu. Dotyczy to w szczególności informacji o celu i podstawie prawnej przetwarzania danych osobowych, okresie ich przechowywania oraz odbiorcach, którym są one przekazywane.

                    2. Kto jest Administratorem Twoich Danych?
                    Administratorem danych osobowych zbieranych jest Monneo S.A. z siedzibą we Wrocławiu (adres siedziby i adres do doręczeń: ul......, .... Wrocław), wpisana do rejestru przedsiębiorców Krajowego Rejestru Sądowego, prowadzonego przez Sąd Rejonowy dla Wrocławia-Fabrycznej we Wrocławiu, VI Wydział Gospodarczy KRS pod numerem KRS 0000561810, NIP: 8943062473 (kontakt pod ww. adresem, adresem poczty elektronicznej: biuro@monneo.pl lub numerem telefonu: (+48) 71 727 68 78 – zwana dalej „Administratorem”.

                    Pod poniższymi danymi kontaktowymi można się również skontaktować z inspektorem ochrony danych.

                    biuro@monneo.pl

                    3. Jak dbamy o Twoje dane?
                    Dane osobowe Klienta są przetwarzane zgodnie z Rozporządzeniem Parlamentu Europejskiego i Rady (UE) 2016/679 z dnia 27 kwietnia 2016 r. w sprawie ochrony osób fizycznych w związku z przetwarzaniem danych osobowych i w sprawie swobodnego przepływu takich danych oraz uchylenia dyrektywy 95/46/WE (Dz.Urz.UE.L Nr 119, str. 1) (dalej również: „RODO”) oraz innymi aktualnie obowiązującymi, tj. przez cały okres przetwarzania określonych danych, przepisami prawa o ochronie danych osobowych. Dane osobowe oznaczają informacje o zidentyfikowanej lub możliwej do zidentyfikowania osobie fizycznej (zwane dalej: „Danymi Osobowymi”). Możliwa do zidentyfikowania osoba fizyczna to osoba, którą można bezpośrednio lub pośrednio zidentyfikować, w szczególności na podstawie identyfikatora takiego jak imię i nazwisko, numer identyfikacyjny, dane o lokalizacji, identyfikator internetowy lub jeden bądź kilka szczególnych czynników określających fizyczną, fizjologiczną, genetyczną, psychiczną, ekonomiczną, kulturową lub społeczną tożsamość osoby fizycznej.

                    Administrator dokłada szczególnej staranności w celu ochrony interesów osób, których dane dotyczą, a w szczególności zapewnia, że zbierane przez niego dane są:

                    przetwarzane zgodnie z prawem, rzetelnie i w sposób przejrzysty dla osoby, której dane dotyczą;
                    zbierane w konkretnych, wyraźnych i prawnie uzasadnionych celach i nieprzetwarzane dalej w sposób niezgodny z tymi celami;
                    adekwatne, stosowne oraz ograniczone do tego, co niezbędne do celów, w których są przetwarzane;
                    prawidłowe i w razie potrzeby uaktualniane;
                    przechowywane w formie umożliwiającej identyfikację osoby, której dane dotyczą, przez okres nie dłuższy, niż jest to niezbędne do celów, w których dane te są przetwarzane;
                    przetwarzane w sposób zapewniający odpowiednie bezpieczeństwo Danych Osobowych, w tym ochronę przed niedozwolonym lub niezgodnym z prawem przetwarzaniem oraz przypadkową utratą, zniszczeniem lub uszkodzeniem, za pomocą odpowiednich środków technicznych lub organizacyjnych.
                    4. W jakich celach informacje o Tobie są wykorzystywane?
                    Każdorazowo cel i zakres danych przetwarzanych przez Administratora wynikają ze zgody Klienta lub przepisów prawa oraz doprecyzowywane są w wyniku działań podejmowanych przez Klienta w ramach komunikacji z Klientem. Przykładowo: (I) Dane Osobowe Klienta mogą być przetwarzane w celu przyznania, prezentowania lub udzielenia mu dedykowanych dla niego ofert i promocji, w możliwie wysokim stopniu dostosowanych do jego preferencji (co może powodować na niego istotny wpływ) wyłącznie jeśli Klient wyraził na to zgodę (niedostępne dla osób, które takiej zgody nie wyraziły);

                    Możliwe cele przetwarzania Danych Osobowych Klientów przez Administratora to w szczególności:

                    podjęcie działań na żądanie przyszłego Klienta przed zawarciem umowy
                    pr ezentowanie reklam, ofert lub promocji (rabatów) dot. produktów lub usług Administratora
                    prowadzenie korespondencji z Klientami, w tym udzielania odpowiedzi na wiadomości Klientów.
                    5. Z jakich informacji o Tobie korzystamy?
                    Administrator może przetwarzać w szczególności następujące Dane Osobowe Klientów: – Dane Osobowe podane w formularzu: imię i nazwisko, adres poczty elektronicznej, stanowisko;

                    6. Czy jesteś zobowiązany do podania nam swoich danych i jakie są ewentualne konsekwencje ich niepodania?
                    Podanie Danych Osobowych przez Klienta jest dobrowolne, jednak jest konieczne do skorzystania z określonych funkcjonalności , np. skorzystania z naszego formularza.

                    7. Na jakiej podstawie prawnej korzystamy z informacji o Tobie?
                    Podstawą przetwarzania Danych Osobowych Klienta jest przede wszystkim konieczność wykonania umowy, której jest stroną lub konieczność podjęcia na jego żądanie działań przed jej zawarciem (art. 6 ust. 1 lit. b) RODO). Dotyczy to przede wszystkim Danych Osobowych podawanych w formularzu. W pozostałych (innych) celach Dane Osobowe Klienta mogą być przetwarzane na podstawie dobrowolnie wyrażonych zgód.

                    8. Czy Twoje dane są przekazywane również do państw trzecich (spoza Europejskiego Obszaru Gospodarczego)?
                    Dane Klienta mogą zostać przekazane do państwa spoza UE, w przypadku podjęcia współpracy przez Administratora z takim podmiotem. W przypadku przekazania danych do państwa trzeciego, w stosunku którego nie została wydana decyzja Komisji Europejskiej stwierdzająca odpowiedni stopień ochrony, Administrator stosuje odpowiednie zabezpieczenia poprzez standardowe klauzule ochrony danych przyjęte przez Komisję Europejską lub organ nadzorczy (zgodnie z art. 46 ust. 2 pkt c) i d) RODO).

                    9. Jakie prawa Ci przysługują?
                    Każdemu Klientowi przysługuje w każdym czasie prawo do:

                    wniesienia skargi do Prezesa Urzędu Ochrony Danych Osobowych;
                    przenoszenia Danych Osobowych, które dostarczył Administratorowi i które są przetwarzane, a przetwarzanie odbywa się na podstawie zgody lub na podstawie umowy, np. do innego administratora;
                    dostępu do Danych Osobowych (w tym np. otrzymania informacji, które Dane Osobowe są przetwarzane);
                    żądania sprostowania i ograniczenia przetwarzania (np. jeśli Dane Osobowe są nieprawidłowe) lub usunięcia Danych Osobowych (np. w przypadku, gdy były one przetwarzane niezgodnie z prawem);
                    cofnięcia każdej wyrażonej Administratorowi zgody w dowolnym momencie, przy czym cofnięcie zgody nie wpływa na przetwarzanie dokonywane przez Administratora zgodnie z prawem przed jej cofnięciem.
                    wniesienia sprzeciwu wobec przetwarzania dotyczących go Danych Osobowych dokonywanego w celu realizacji prawnie uzasadnionych interesów Administratora lub strony trzeciej, w tym w szczególności wobec przetwarzania na potrzeby marketingu, (jeżeli nie ma innych ważnych prawnie uzasadnionych podstaw przetwarzania nadrzędnych wobec interesów Klienta).
                    

                    10. Przez jaki okres będziemy przechowywać Twoje dane?
                    W przypadku działań marketingowych – do czasu wniesienia przez Klienta sprzeciwu, a jeśli związane są z technologią cookies i podobnymi, w zależności od kwestii technicznych, do czasu usunięcia tych plików przy pomocy ustawień przeglądarki / urządzenia (przy czym usunięcie plików nie zawsze jest tożsame z usunięciem Danych Osobowych uzyskanych za pośrednictwem tych plików, stąd możliwość sprzeciwu).

                    Jeżeli przetwarzanie Danych Osobowych jest uzależnione od zgody Klienta, Dane Osobowe mogą być przetwarzane dopóki nie zostanie ona cofnięta.

                    

                    11. Czy będą do Ciebie przesyłane informacje handlowe (np. na Twój adres e-mail)?
                    Administrator posiada techniczną możliwość komunikowania się z Klientem na odległość (np. wiadomość e-mail). Informacje handlowe związane z prowadzoną przez Administratora działalnością komercyjną mogą być przesyłane wyłącznie na podstawie wyrażonej przez Klienta zgody.
                    
                    12. Jak zabezpieczamy Twoje dane?
                    Administrator uwzględniając stan wiedzy technicznej, koszt wdrażania oraz charakter, zakres, kontekst i cele przetwarzania oraz ryzyko naruszenia praw lub wolności osób fizycznych o różnym prawdopodobieństwie wystąpienia i wadze zagrożenia, stosuje odpowiednie środki techniczne i organizacyjne zapewniające ochronę przetwarzanych Danych Osobowych odpowiednią do zagrożeń oraz kategorii danych objętych ochroną, a w szczególności zabezpiecza dane przed ich udostępnieniem osobom nieupoważnionym, zabraniem przez osobę nieuprawnioną, przetwarzaniem z naruszeniem obowiązujących przepisów oraz zmianą, utratą, uszkodzeniem lub zniszczeniem. Udostępnianie na zewnątrz informacji o stosowanych środkach technicznych i organizacyjnych zapewniających ochronę przetwarzania może osłabić ich skuteczność przez co zagraża właściwej ochronie Danych Osobowych.
                    </p>
                </Col>
            </Row>
        </Container>            
    )
}

export default Policy;